<template>
  <div class="intelligent-device">
    <SmallModule title="发电效率(PR)">
      <template #titleRight>
        <div class="right-title-layout">
          <a-radio-group
            class="operation-screen-radio-group"
            v-model:value="searchInfo.sysValue"
            size="small"
            @change="handleSystemRateChange"
          >
            <a-radio-button value="inThoseMonth">月</a-radio-button>
            <a-radio-button value="inThoseYear">年</a-radio-button>
          </a-radio-group>
        </div>
      </template>
      <template #content>
        <div class="content-layout">
          <div style="transform: scale(0.85); transform-origin: 0 0">
            <EchartPillarChart
              :data="pchartData"
              :colors="pchartData.colors"
              :width="`26.14vw`"
              :height="`28.06vh`"
              :name="pChartName"
              :dataZoomDisabled="false"
              :dataZoomMaxValueSpan="pchartData.categories.length"
              :dataZoomDefaultValueRange="[0, 6]"
            />
          </div>
        </div>
      </template>
    </SmallModule>
  </div>
</template>

<script lang="ts" setup>
  import { reactive, UnwrapRef, onMounted, ref } from 'vue';
  import SmallModule from '/@/components/Framework/OperationScreen/SmallModule.vue';
  import EchartPillarChart from '/@/components/Framework/Chart/EchartPillarChart.vue';
  import { getPowerEfficiency } from './data';

  const searchInfo: UnwrapRef<any> = reactive({});

  onMounted(() => {
    searchInfo.sysValue = 'inThoseMonth';
    queryPowerEfficiency();
  });

  const pchartData = ref({
    barData: [],
    categories: [],
    units: ['%'],
    colors: [['#16CFFE', '#08FFF6']],
  });
  const pChartName = ['系統效率'];

  const queryPowerEfficiency = async () => {
    searchInfo.dateType = searchInfo.sysValue;
    let data = await getPowerEfficiency(searchInfo);
    if (data.result) {
      // pchartData.value.lineData[0] = data.result.lineData;
      pchartData.value.barData = data.result.barData;
      pchartData.value.categories = data.result.categories;
    }
  };

  const handleSystemRateChange = (e: Event) => {
    searchInfo.sysValue = e.target.value;
    queryPowerEfficiency();
  };
</script>

<style lang="less" scoped>
  .right-title-layout {
    padding-right: 0.16rem;
  }

  .content-layout {
    padding: 0 0.16rem;
  }
</style>
